// Import Styled
import styled from 'styled-components';

export const BlockDefault = styled.div`
	.name{
		display: inline-block;
		margin-left: 12px;
    	vertical-align: top;
    	margin-bottom: 24px;
	}
	.number{
		text{
			float: right;
		}
	}	
	.tag{
		text{
			float: right;
		}
		& > div{
			display: flex;
		}
	}
	.tags{
		text{
			margin-right: 12px;
		}
	}
`;